@use '../global' as g;

/*********************************
    ボタン
*********************************/

/* ボタン */
.button{
    display: inline-block;
    min-width: 150px;
    padding: 5px 10px;
    background-color: g.$color_grey_dark;
    border: 1px solid transparent;
    border-radius: 4px;
    box-shadow: 0 1px 4px 0 rgba(#000000,0.2);
    color: g.$color_white;
    font-size: g.$font-size_lg;
    font-weight: bold;
    text-align: center;
    transition: 0.5s;

    &:hover,&:focus{ 
        background-color: g.$color_grey_light;
        border-color: currentColor;
        color: g.$color_grey_dark;
    }

    &--chevron-right{
        position: relative;
        padding-right: 2em;
        padding-left: 1.2em;

        &::after{
            font-family: 'Font Awesome 6 Free';
            font-weight: 900;
            content: "\f054";  
            position: absolute;
            top: 50%;
            right: 0.8rem;
            transform: translateY(-50%); 
        }   
    }
    
    &--large{
        min-width: 180px;
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: g.$font-size_xl;
    }

    
    &--red{
        background-color: #9b3939;
        
        &:hover,&:focus{ 
            color: #9b3939;
        }
    }
}

/* アイコン付き小ボタン */
.before-icon-button{
    display: inline-block;
    padding: 0.2em 0.4em;
    background-color: g.$color_white;
    border: 1px solid currentColor;;
    border-radius: 2px;
    box-shadow: 0 1px 4px 0 rgba(#000000,0.1);
    color: g.$color_grey_dark;
    transition: 0.5s;

    &::before{
        display: inline-block;
        font-family: 'Font Awesome 6 Free';
        font-weight: 900;
        margin-right: 0.4em; 
    }  

    &:hover,&:focus{ 
        background-color: g.$color_grey_dark;
        border-color: transparent;
        color: g.$color_white;
    }

    &--download{
        
        &::before{
            content: "\f019"; 

        }
    }

    &--zoom{
        
        &::before{
            content: "\f00e"; 

        }
    }
}